.transactionhist {
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 30px;
  width: 900px;
  font-family: "Lucida Sans Unicode", "Lucida Grande", Sans-Serif;
  font-size: 14px;
  text-align: center;
  text-transform: uppercase;
}

.names {
  width: 300px;
  height: 50px;
  padding: 0;
  margin: 0;
  background: #3cd1f7;
  color: white;
}

.transactions {
  height: 50px;
}

.transactions:nth-child(even) {
  background-color: #e9dede;
}

.information {
  outline: 1px solid grey;
}

.value {
  color: black;
  width: 300px;
  outline: 1px solid grey;
}
